import React, { useEffect,useState,useRef  } from 'react';

import Cookies from 'universal-cookie';
import { Link ,useParams,useNavigate} from 'react-router-dom';
import axios from 'axios'
import {Tabs,Tab} from 'react-bootstrap';
var hostapi=process.env.REACT_APP_API_URL;

export default function Profile(props){
  const params= useParams();
  const [idUser, setIdUser] = useState(params["idUser"]);
  const [productsSell,setProductsSell]=useState([]);
  const [productsBuy,setProductsBuy]=useState([]);
  

  const windowSize = useRef([window.innerWidth, window.innerHeight]);
  const cookies = new Cookies();

  const [previewImage,setPreviewImage]=useState();
  const [name,setName]=useState("");
  const [surname,setSurname]=useState("");
  const [pathPhoto,setPathPhoto]=useState("");
  const [university,setUniversity]=useState("");
  
  const navigate=useNavigate();
  useEffect(async()=>{
      setPreviewImage( (cookies.get("pathPhoto")==null || cookies.get("pathPhoto")=="")?hostapi+"/account.png":hostapi+"/image/profile/"+ cookies.get("pathPhoto"));
      

        //axios.get(hostapi+'/detailProfile/'+cookies.get("idUser")+"/"+cookies.get("idsession"))
        
        await axios({  
            method: 'GET',
            url:hostapi+'/detailPublicProfile/'+cookies.get("idUser")+"/"+cookies.get("idsession")+"/"+idUser,
            headers: {
              'Content-Type': 'application/json'
            },
          }).then(res=>{
            console.log(res.data[0]);
            var userInformation=res.data[0];
            setName(userInformation.name)
            setSurname(userInformation.surname);
            setPathPhoto(userInformation.pathPhoto);
            setPreviewImage( (userInformation.pathPhoto==null || userInformation.pathPhoto=="")? "/account.png" : hostapi+"/image/profile/"+ userInformation.pathPhoto); 
            //setPreviewImage( "/account.png")
            setUniversity(userInformation.university)

          })

      await axios({  
        method: 'GET',
        url: hostapi+'/products/'+"sell"+"/"+cookies.get("idUser")+"/"+cookies.get("idsession")+"/"+idUser+'/1/100',
        headers: {
          'Content-Type': 'application/json'
        },
  
      }).then(res=>{ 
       setProductsSell(res.data);
      //setIdUser(props.idUser);
       console.log(res.data);
     })

     await axios({  
      method: 'GET',
      url: hostapi+'/products/'+"buy"+"/"+cookies.get("idUser")+"/"+cookies.get("idsession")+"/"+idUser+'/1/100',
      headers: {
        'Content-Type': 'application/json'
      },

    }).then(res=>{ 
      setProductsBuy(res.data);
    //setIdUser(props.idUser);
     console.log(res.data);
   })
   // }, 1000);
  //  return () => clearInterval(interval);
    
   
  },[]);
  function showProduct(products){
    var resultData=[]
    console.log("pas"+products.length)
    for(var i=0;i<products.length;i++){
      resultData.push( 
        <a href={"/detailProduct/"+products[i].idProduct} className='linkPost' style={{textDecoration: "none"}}>
      <div style={{display: "flex", flexDirection: "column"}}>
      
        <div class="card" style={{backgroundSize: "cover", backgroundColor:" rgb(0, 0, 0)", backgroundImage: "url('"+hostapi+"/image/product/"+products[i].pathImage+"')", width: "220px",height: "389px", borderRadius: "10px", padding: "10px", display: "flex", flexDirection: "column", justifyContent: "space-between", marginRight: "20px", marginLeft: "0px", marginBottom: "16px", color: "rgb(255, 255, 255)", textShadow: "rgb(0, 0, 0) 2px 2px 10px", cursor: "pointer"}} >
      
          <div  style={{background: "linear-gradient(rgba(0, 0, 0, 0.25), transparent 50%)", borderRadius: "10px", position: "absolute", marginTop: "-10px", width: "220px",height: "389px", marginLeft: "-11px"}} >

          </div>
          
            <div style={{display: "flex", justifyContent: "space-between", alignItems: "baseline", fontSize: "10px", position: "inherit", zIndex: "1"}}>
              <a href="/detailProduct" />
              <div style={{backgroundColor: "rgb(0, 0, 0)", borderRadius: "50px", paddingRight: "6px", minWidth: "58px", textAlign: "center", display: "flex", alignItems: "center"}}>
              
              </div>
              <div style={{display: "flex",alignItems: "center", flexDirection: "column"}} >
                <div>
                  <i class="fas fa-heart" style={{fontSize:"20px"}}></i>
                </div>
                <font style={{verticalAlign: "inherit"}} >
                  <font style={{verticalAlign: "inherit"}}>0</font>
                </font>
              </div>            
        </div>

        <div>
                  <div class="black-color" style={{fontSize:"20px"}}>Presiona Para ver Detalles</div>
                </div>
            </div>
    

      
      
      <div style={{width:"158px",marginLeft:"0",color:"#000000",cursor:"pointer"}}>
        <div style={{textAlign: "center",overflow:"hidden",lineHeight:"0.8",textOverflow:"ellipsis",display:"-webkit-box",webkitLineClamp:"2",webkitBoxOrient:"vertical",fontSize:"18px"}}> Q.{products[i].price}</div>
        <div style={{display:"flex",alignItems:"center"}}><img src={(products[i].pathPhoto=="" || products[i].pathPhoto==null)?"/account.jpg": hostapi+"/image/profilel/"+products[i].pathPhoto} height="24" width="24" alt="dr_applesauce profile image" style={{height:"24px",width:"24px",backgroundColor:"#000000",borderRadius:"12px"}}/><div style={{marginLeft:"8px",fontSize:"12px"}}>{products[i].fullname }</div></div><div style={{width:"auto",height:"10px"}}></div><div style={{overflow:"hidden",lineHeight:"1.2",textOverflow:"ellipsis",display:"-webkit-box","-webkit-line-clamp":"2",WebkitBoxOrient:"vertical",fontSize:"13px"}}>{products[i].description}</div>
      </div>
      <div style={{color:"#070707",marginTop:"8px",marginBottom:"28px",fontSize:"11px"}}></div>
    </div>
    </a>)
    }
    return resultData;
}
          return (
<>
<link href="/wizard.css" rel="stylesheet"/>
<div class="row featurette">

<form action="" method="post">
  

            <div class="preview text-center d-flex justify-content-center">
                <img class="preview-img" src={(previewImage==null)? "/account.png":  previewImage} alt="Preview Image" width="200" height="200"/>
                <span class="Error"></span>
            </div>
            <div class="preview text-center d-flex justify-content-center">
              {name+" "+surname}
            </div>
            <div class="preview text-center d-flex justify-content-center">
              {university}
            </div>
        </form>
        <Tabs
      defaultActiveKey="profile"
      id="uncontrolled-tab-example"
      className="mb-3"
    >
      <Tab eventKey="home" title="Vendido">
      <div style={{display:"flex",position:"relative",paddingTop:"5px",fontSize:"12px",flexWrap:"wrap"}} >
        {
            showProduct(productsSell)
          
        }
       
        

        
      </div>
      </Tab>
      <Tab eventKey="profile" title="Comprado">
      <div style={{display:"flex",position:"relative",paddingTop:"5px",fontSize:"12px",flexWrap:"wrap"}} >
        {
            showProduct(productsBuy)
          
        }
       
        

        
      </div>
      </Tab>
    </Tabs>
        

     
      
      
    </div>
</>
            );
      } 
  