import React, { useState } from 'react';
//import '../App.css';
import Cookies from 'universal-cookie';
import { useParams,Link ,useNavigate} from 'react-router-dom';
import axios from 'axios'
var hostapi=process.env.REACT_APP_API_URL;
  

export default function Recovery(props){
  const params= useParams()
  const [email, setEmail] = useState("");
  const [clave, setClave] = useState("");
  const [clave2, setClave2] = useState("");
  const [tokenRecovery,setTokenRecovery]=useState(params["tokenRecovery"]);
  const [idUser,setIdUser]=useState(params["idUser"]);
  const [isSendToken,setIsSendToken]=useState(false);
  const navigate=useNavigate();
  
  const sendRecovery=async(evt)=>{
    evt.preventDefault();
    if(idUser==null && tokenRecovery==null){
       /* axios.post(hostapi+'/recoveryEmailPassword',{
           // email: email
        }//,
        // headers: { "Content-Type": "multipart/form-data" }
        )*/
        await axios({  
          method: 'POST',
          url:hostapi+'/recoveryEmailPassword',
          data:{
            email:email
        },
          headers: {
            'Content-Type': 'application/json'
          },
    
        }).then(res=>{
          console.log(res.data)
          setIsSendToken(true)
          //setSessionCookie(res.data)
        })
   }else{
       /* axios.post(hostapi+'/resetEmailPassword',{
          clave:clave,
          idUser:idUser,
          recoveryToken:tokenRecovery
        }//,
        // headers: { "Content-Type": "multipart/form-data" }
        )*/
        
        await axios({  
          method: 'POST',
          url:hostapi+'/resetEmailPassword',
          data:{
            clave:clave,
            idUser:idUser,
            recoveryToken:tokenRecovery
          },
          headers: {
            'Content-Type': 'application/json'
          },
    
        }).then(res=>{
          console.log(res.data)
          setIsSendToken(true)
          setSessionCookie(res.data)
        })  
   }
  }
 const  setSessionCookie=(json)=>{
    if(json.status=="success"){
        const cookies = new Cookies();
        cookies.set("idsession", json.idsession, { path: '/' ,secure:true});
        cookies.set("idUser", json.idUser, { path: '/' ,secure:true});
        cookies.set("pathPhoto", json.pathPhoto, { path: '/' ,secure:true});
       // cookies.set("isFirstlogin", json.isFirstlogin, { path: '/' ,secure:true});
      //  this.props.history.push("/");
      navigate("/");
      console.log("d");
    }else{
       
    }
  }
    return(
<>
<link href="/assets/dist/css/bootstrap.min.css" rel="stylesheet" id="bootstrap-css"/>
<script src="/assets/dist/js/bootstrap.bundle.min.js"></script>
<script src="https://cdnjs.cloudflare.com/ajax/libs/jquery/3.2.1/jquery.min.js"></script>

<link href="/login.css" rel="stylesheet"></link>
<div className="wrapper fadeInDown">
  <div id="formContent">
    <div className="fadeIn first">
      <h1>efectUp</h1>
      <h>{tokenRecovery == undefined && isSendToken==false  && "Recuperacion de contraseña"}
        {tokenRecovery != undefined && isSendToken==false  && "Ingresa nueva contraseña para poder ingresar de nuevo"}
      </h>
    </div>

    <form >
    {tokenRecovery == undefined && isSendToken!=false && <h>Hemos enviado un correo de Recuperacion de contraseña al correo registrado </h> }
      {tokenRecovery == undefined && isSendToken==false  && <input type="email" id="login"  onChange={e=>setEmail(e.target.value)} className="fadeIn second" name="email" placeholder="correo "/>}
      
      {tokenRecovery != undefined && isSendToken==false  && <input type="password" id="password" onChange={e=>setClave(e.target.value)} className=" fadeIn third" name="clave" placeholder="password"/>}
      {tokenRecovery != undefined && isSendToken==false  && <input type="password" id="password2"  onChange={e=>setClave2(e.target.value)} className=" fadeIn third" name="clave2" placeholder="otras vez el password"/>}
      {isSendToken==false  && <input type="submit" onClick={sendRecovery}  className="fadeIn fourth" value="Recuperar"/>}
    </form>

    <div id="formFooter">
      <a className="underlineHover" href="/login">Iniciar Sesion</a>
    </div>

  </div>
</div>
</>
    );
  }
