import React, { useEffect,useState  } from 'react';

import { Link,useParams,useNavigate } from 'react-router-dom';
import axios from 'axios'
import Cookies from 'universal-cookie';

var hostapi=process.env.REACT_APP_API_URL;

export default function PostProduct(props){
  const params= useParams();
  const [idPostUpdate, setIdPostUpdate] = useState(params["idProduct"]);
  const [idUser, setIdUser] = useState("");
  const [title, setTitle] = useState("");
  const [price, setPrice] = useState("");
  const [state, setState] = useState("");
  const [description, setDescription] = useState("");
  const [selectedFile, setSelectedFile] = useState(null);
  const [previewImage,setPreviewImage]=useState();
  const [isImage, setIsImage] = useState("");
  const cookies = new Cookies();
  const navigate=useNavigate();

  const sendPost=async(evt)=>{
    evt.preventDefault();
    const formData=new FormData();
    formData.append("idUser",cookies.get("idUser"));
    formData.append("idSession",cookies.get("idsession"));
    formData.append("title",title);
    formData.append("price",price);
    formData.append("state",state);
    formData.append("description",description);
    if(isImage)
      formData.append("photoFile",selectedFile);
    else 
      formData.append("photoFile",null);
    
    if(idPostUpdate!=null){  //update
      formData.append("idProduct",idPostUpdate);


     /* axios.put(hostapi+'/product',formData //,
      // headers: { "Content-Type": "multipart/form-data" }
      )*/
      await axios({   //reparar manyana
      method: 'PUT',
      url: hostapi+'/product',
      data: formData,

      headers: {
        'Content-Type': 'multipart/form-data'
      },

    }).then(res=>{
        //console.log("d")
        //navigate("/");
      }).catch(
        (err)=>console.log("File upload Error")
        );
        navigate("/detailProduct/"+idPostUpdate);
    }else{
      //axios.post(hostapi+'/product',formData //,
      // headers: { "Content-Type": "multipart/form-data" }
      //) 
      await axios({   //reparar manyana
        method: 'POST',
        url: hostapi+'/product',
        data: formData,
  
        headers: {
          'Content-Type': 'multipart/form-data'
        },
  
      }).then(res=>{
        //console.log("d")
        //navigate("/");
      }).catch(
        (err)=>console.log("File upload Error")
        );
        navigate("/");
    }
  }
useEffect(()=>{
  console.log(idPostUpdate)
  if(idPostUpdate!=null){
    axios.get(hostapi+"/detailproduct/"+idPostUpdate+"/"+cookies.get("idUser")+"/"+cookies.get("idsession")).then(res=>{
      if(res.data[0]==null || res.data[0].idUser!=cookies.get("idUser"))
          navigate("/");
     // setProduct(res.data[0])
      console.log(res.data[0]);
      //isBuyer()
      setTitle(res.data[0].title);
      setPrice(res.data[0].price)
      setDescription(res.data[0].description)
      setPreviewImage(hostapi+"/image/product/"+res.data[0].pathImage);
      setIsImage(false);
  });
  }

},[]);
const setImageFile=(e) =>{
  setIsImage(true);
  setSelectedFile(e.target.files[0])
  setPreviewImage(URL.createObjectURL(e.target.files[0])); 
}
      
          return (
<>
<div className="row featurette">
      <div className="container-fluid">
        <form className="row text-white ">
          <div className="col-5  border">
            <div className="drop-zone" style={{position:"relative"}} >
              
              <span className="drop-zone__prompt" style={{position:"absolute"}}>has click para ingresar una imagen
                <div class="browse-button" style={{width: "100%",height: "100%",top: "10px",background: "linear-gradient(180deg, transparent, black)",opacity: "0",transition: "0.3s ease"}} >
              </div>
              </span>
              {previewImage!=null && <img class="preview-img" src={ previewImage} alt="Preview Image" style={{width: "100%",height:"100%",margin: "auto" ,position: "absolute"}} /> }
              <input type="file"  onChange={setImageFile} style={{ width: "100%",position: "absolute",height:"100%",transform: "translate(-1px,-26px)",opacity: "0"}} name="photoFile" class="browse-input"/>
              
            </div>
          </div>
          <div className="col-7 border">
            <div className="form-control">
              <label for="titulo">Titulo</label>
              <input type="text" onChange={e=>setTitle(e.target.value)} value={title} className="form-control" id="titulo" name="titulo" />
            
            
              <label for="precio">Precio: Q.</label>
              <input type="text" onChange={e=>setPrice(e.target.value)} value={price} pattern="\Q?\d{1,3}(,?\d{3})*(\.\d{1,2})?" className="form-control" id="precio"   name="cantidad" />
              
              <div className="form-group">
                <label for="precio">Estado de articulo</label>
                <select onChange={e=>setState(e.target.value)}   className="form-control" id="exampleFormControlSelect1">
                  <option>Segunda Mano</option>
                  <option>Experiencia/Actividad</option>
                </select>
              </div>
            
              <label for="descripcion">Descripcion</label>
              <textarea onChange={e=>setDescription(e.target.value)} value={description} className="form-control" id="descripcion" name="descripcion" ></textarea>
              <button onClick={sendPost} className="form-control btn btn-primary">Publicar</button>
            </div>
          </div>
        </form>
      </div>
        
     

      
      <script src="postProduct.js"></script>
      <link rel="stylesheet" href="/postProduct.css"></link>
      
    </div>
</>
            );
      } 
  