import React, { useState } from 'react';
//import '../App.css';
import Cookies from 'universal-cookie';
import { Link ,useNavigate} from 'react-router-dom';
import axios from 'axios'
var hostapi=process.env.REACT_APP_API_URL;
  

export default function Register(props){
  const [email, setEmail] = useState("");
  const [clave, setClave] = useState("");
  const [clave2, setClave2] = useState("");
  const [activity, setActivity] = useState("Comprar");

  const navigate=useNavigate();
  
  const sendRegister=evt=>{
    evt.preventDefault();
    axios.post(hostapi+'/register',{
        email:email,
        clave:clave,
        activity:activity
        
    }//,
    // headers: { "Content-Type": "multipart/form-data" }
    ).then(res=>{
      console.log(res.data)
      setSessionCookie(res.data)
    })
  }
 const  setSessionCookie=(json)=>{
    if(json.status=="success"){
        const cookies = new Cookies();
        cookies.set("idsession", json.idsession, { path: '/' ,secure:true});
        cookies.set("idUser", json.idUser, { path: '/' ,secure:true});
       // cookies.set("isFirstlogin", json.isFirstlogin, { path: '/' ,secure:true});
      //  this.props.history.push("/");
      navigate("/wizard");
      console.log("d");
    }else{
       
    }
  }
    return(
<>
<link href="/assets/dist/css/bootstrap.min.css" rel="stylesheet" id="bootstrap-css"/>
<script src="/assets/dist/js/bootstrap.bundle.min.js"></script>
<script src="https://cdnjs.cloudflare.com/ajax/libs/jquery/3.2.1/jquery.min.js"></script>

<link href="/login.css" rel="stylesheet"></link>
<div className="wrapper fadeInDown">
  <div id="formContent">
    <div className="fadeIn first">
      <h1>efectUp </h1>
      <h>registrate gratis, compra y vende Ropa de Segunda Mano, embajadores de experiencias</h>
    </div>

    <form >
      <input type="email" id="login"  onChange={e=>setEmail(e.target.value)} className="fadeIn second" name="email" placeholder="correo Daniel@gmail.com"/>
      <input type="password" id="password" onChange={e=>setClave(e.target.value)} className="fadeIn third" name="clave" placeholder="password"/>
      <input type="password" id="password2"  onChange={e=>setClave2(e.target.value)} className="fadeIn third" name="clave2" placeholder="otras vez el password"/>
       <div className="form-group">
                <label for="titleactivity">Que quieres hacer:</label>
                <select  onChange={e=>setActivity(e.target.value)}   className="form-control" sytle={{size:10}} id="nameActivity">
                  <option value='Comprar'>Comprar</option>
                  <option value='Vender'>Vender</option>
                </select>
       </div>
      <a href="/terms" >Al registrarte estas aceptando los Terminos y Condiciones</a>
      <input type="submit" onClick={sendRegister} className="fadeIn fourth" value="Registrarse"/>
  
    </form>

    <div id="formFooter">
      <a className="underlineHover" href="/login">Iniciar Sesion</a>
    </div>

  </div>
</div>
</>
    );
  }
