import React, { useState } from 'react';
//import '../App.css';
import Cookies from 'universal-cookie';
import { Link,useNavigate,useLocation   } from 'react-router-dom';
import BackgroundPage from './backgroundPage'

import axios from 'axios'
import Initial from './initial';
var hostapi=process.env.REACT_APP_API_URL;
  

export default function Login(props){
  const [email,setEmail]=useState("");
  const [clave,setClave]=useState("");
  const [correoState,setCorreoState]=useState("hidden");
  const [passwordState,setPasswordState]=useState("hidden");
  const navigate=useNavigate();

  if (typeof document !== 'undefined') {
   // var isClick = 'onclick' in document.documentElement;
   var isTouch= 'ontouchstart' in document.documentElement;
    //console.log("isClick ="+isClick);
  }

  const sendLogin=async(evt)=>{
    evt.preventDefault();    
   /* axios.post(hostapi+'/login',{
        email:email,
        clave:clave
    }//,
    // headers: { "Content-Type": "multipart/form-data" }
    )*/
    await axios({  
      method: 'POST',
      url: hostapi+'/login',
      data: {
        email:email,
        clave:clave
    },

      headers: {
        'Content-Type': 'application/json'
      },

    }).then(res=>{
     // console.log(res.data);
      setSessionCookie(res.data,res);
     
    })
  
  }

  const  setSessionCookie=(json,res)=>{
    setCorreoState("hidden");
    setPasswordState("hidden");
    if(json.status=="success"){
        const cookies = new Cookies();
        cookies.set("idsession", json.idsession, { path: '/' ,secure:true});
        cookies.set("idUser", json.idUser, { path: '/' ,secure:true});
        cookies.set("pathPhoto", json.pathPhoto, { path: '/' ,secure:true});
       // cookies.set("isFirstlogin", json.isFirstlogin, { path: '/' ,secure:true});c
        navigate("/");
       //return <BackgroundPage page={Initial} />
       console.log("d");
    }else if(json.status=="bad_Correo"){
        setCorreoState("");
        setEmail("");
        setClave("");
    }else if(json.status=="fail_password"){
        setPasswordState("");
        setClave("");
    }
  }
    return(
<>
<link href="/assets/dist/css/bootstrap.min.css" rel="stylesheet" id="bootstrap-css"/>
<script src="/assets/dist/js/bootstrap.bundle.min.js"></script>
<script src="https://cdnjs.cloudflare.com/ajax/libs/jquery/3.2.1/jquery.min.js"></script>

<link href="/login.css" rel="stylesheet"></link>
<div className="wrapper fadeInDown">
  <div id="formContent">
    <div className="fadeIn first">
      <h1>efectUp</h1>
      <h>compra y vende Ropa de segunda Mano, experiencias de viaje con confianza</h>
    </div>

    <form>
      <input type="email" id="login" value={email} onChange={e=>setEmail(e.target.value)} className="fadeIn second" name="login" placeholder="correo"/>
      <label className={"input_error visually-"+correoState}>Correo Incorrecto</label>
      <input type="password" id="password" value={clave} onChange={e=>setClave(e.target.value)} className="fadeIn third" name="login" placeholder="password"/>
      <label className={"input_error visually-"+passwordState}>Contraseña Incorrecta</label>
      <input type="submit"  onClick={sendLogin} className="fadeIn fourth login" value="Ingresar"/>
    
    </form>
    <a className="underlineHover" href="/recovery">¿Olvidaste tu contraseña?</a>
    <div id="formFooter">
      <a className="underlineHover" href="/register">Registrarse</a>
    </div>

  </div>
</div>
</>
    );
  }
