import React, { useEffect,useState  } from 'react';



import { Link,useParams,useNavigate } from 'react-router-dom';
import 'react-slideshow-image/dist/styles.css'
import { Slide } from 'react-slideshow-image';
import Cookies from 'universal-cookie';
import axios from 'axios';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
var hostapi=process.env.REACT_APP_API_URL;

const slideImages = [
    'https://i.imgur.com/KZpuufK.jpg',
    'https://i.imgur.com/GwiUmQA.jpg',
    'https://i.imgur.com/DhKkTrG.jpg'
  ];
  

export default function ProductPage(props){
    const params= useParams()
    const cookies = new Cookies();
    const navigate=useNavigate();
    const [idProduct,setIdproduct]=useState(params["idProduct"]);
    const [idUser,setIduser]=useState("");
    const [idsession,setIdsession]=useState("");
    const [product,setProduct]=useState( {
        "description": "",
        "phone":"",
        "idProduct": -1,
        "idUser": -1,
        "pathImage": "",
        "fullname":"",
        "price": "",
        "state": "",
        "statePublic": 0,
        "title": ""
    });
    const [buyerIs,setBuyerIs]=useState("");
    const [pathImage,setPathImage]=useState("");
    const [title,setTitle]=useState("");
    const [price,setPrice]=useState("");
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const stateProduct=["Usado","Nuevo","refurshed"]
    //setIdproduct(params["idProduct"])
    const buyProduct=async(evt)=>{
        evt.preventDefault();
        if(idUser!=null && idsession!=null){
           /* axios.post(hostapi+'/buy/'+idProduct,{
                idUser:cookies.get("idUser"),
                idsession:cookies.get("idsession")
            }
            )*/
            await axios({  
                method: 'POST',
                url: hostapi+'/buy/'+idProduct,
                data: {
                    idUser:cookies.get("idUser"),
                    idsession:cookies.get("idsession")
                },
          
                headers: {
                  'Content-Type': 'application/json'
                },
          
              }).then(res=>{
              console.log(res.data);
              
              //navigate("/")
              if(res.data.status=="fail")setShow(true);
              else navigate("/")
            })
        }else{
            navigate("/login")
        }
       
    }
    const isBuyer= async () =>{
       /* axios.get(hostapi+'/isbuyer/'+idProduct+"/"+cookies.get("idUser")+"/"+cookies.get("idsession")
        )  */
        
        await axios({  
            method: 'GET',
            url: hostapi+'/isbuyer/'+idProduct+"/"+cookies.get("idUser")+"/"+cookies.get("idsession"),
      
            headers: {
              'Content-Type': 'application/json'
            },
      
          }).then(res=>{
          console.log(res.data);
          if(res.data.state=="Yes"){
              setBuyerIs("Yes")
              return true;
            }
          else{
                setBuyerIs("No")
              return false;
            }
        })
    }
    useEffect(()=>{
        
        setIduser(cookies.get("idUser"))
        const consultaAPI = async () => {          
           // axios.get(hostapi+"/detailproduct/"+idProduct+"/"+cookies.get("idUser")+"/"+cookies.get("idsession"))
            await axios({  
                method: 'GET',
                url: hostapi+"/detailproduct/"+idProduct+"/"+cookies.get("idUser")+"/"+cookies.get("idsession"),
          
                headers: {
                  'Content-Type': 'application/json'
                },
          
              }).then(res=>{
                if(res.data[0]==null)
                    navigate("/");
                setProduct(res.data[0])
                console.log(product);
                isBuyer()
                setTitle(res.data[0].title);
                //setPrice(res.data[0].)
                setPathImage(hostapi+"/image/product/"+res.data[0].pathImage);
            });
         }
         consultaAPI();
         
    },[]);
      
    
          return (
<>
<div className="row featurette">


      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>efectUp</Modal.Title>
        </Modal.Header>
        <Modal.Body>No cuentas con la cantidad Monetaria Necesaria para la compra, necesitas Depositar para agregar Saldo!</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Cerrar
          </Button>
          <Button variant="primary" onClick={()=>navigate("/monetization")}>
            ir a Depositar
          </Button>
        </Modal.Footer>
      </Modal>
    

     <div className="container-fluid mt-2 mb-3">
        <div className="row no-gutters">
            <div className="col-md-5 pr-2">
                <div className="card">
                    <div className="demo" >
                    <Slide easing="ease">
          <div className="each-slide">
            <div style={{'backgroundImage': "url("+pathImage+")"}} >
            </div>
          </div>
          
        </Slide>

                    </div>
                </div>
                {"todavia "=="no" && <div className="card mt-2">
                    <h6>Opiniones</h6>
                    <div className="d-flex flex-row">
                        <div className="stars"> <i className="fa fa-star"></i> <i className="fa fa-star"></i> <i className="fa fa-star"></i> <i className="fa fa-star"></i> </div> <span className="ml-1 font-weight-bold">4.6</span>
                    </div>
                    <hr/>
                    <div className="badges"> <span className="badge bg-dark ">All (230)</span> <span className="badge bg-dark "> <i className="fa fa-image"></i> 23 </span> <span className="badge bg-dark "> <i className="fa fa-comments-o"></i> 23 </span> <span className="badge bg-warning"> <i className="fa fa-star"></i> <i className="fa fa-star"></i> <i className="fa fa-star"></i> <i className="fa fa-star"></i> <span className="ml-1">2,123</span> </span> </div>
                    <hr/>
                    <div className="comment-section">
                        <div className="d-flex justify-content-between align-items-center">
                            <div className="d-flex flex-row align-items-center"> <img src="https://i.imgur.com/o5uMfKo.jpg" className="rounded-circle profile-image"/>
                                <div className="d-flex flex-column ml-1 comment-profile">
                                    <div className="comment-ratings"> <i className="fa fa-star"></i> <i className="fa fa-star"></i> <i className="fa fa-star"></i> <i className="fa fa-star"></i> </div> <span className="username">Lori Benneth</span>
                                </div>
                            </div>
                            <div className="date"> <span className="text-muted">2 May</span> </div>
                        </div>
                        <hr/>
                        <div className="d-flex justify-content-between align-items-center">
                            <div className="d-flex flex-row align-items-center"> <img src="https://i.imgur.com/tmdHXOY.jpg" className="rounded-circle profile-image"/>
                                <div className="d-flex flex-column ml-1 comment-profile">
                                    <div className="comment-ratings"> <i className="fa fa-star"></i> <i className="fa fa-star"></i> <i className="fa fa-star"></i> <i className="fa fa-star"></i> </div> <span className="username">Timona Simaung</span>
                                </div>
                            </div>
                            <div className="date"> <span className="text-muted">12 May</span> </div>
                        </div>
                    </div>
                </div>}
            </div>
            <div className="col-md-7">
                <div className="card">
                    <div className="d-flex flex-row align-items-center">
                        <div className="p-ratings"> <i className="fa fa-star"></i> <i className="fa fa-star"></i> <i className="fa fa-star"></i> <i className="fa fa-star"></i> </div> <span className="ml-1">5.0</span>
                    </div>
                    <div className="about"> <span className="font-weight-bold">{product.title}</span>
                        <h4 className="font-weight-bold">Q.{product.price}</h4>
                    </div>
                    <div className="buttons">
                    { product.statePublic==1 && <> <button onClick={buyProduct} style={{background:"#ff8000"}} className="btn btn-primary btn-long buy">Comprar</button> <button className="btn btn-light wishlist"> <i className="fa fa-heart"></i> </button> </>}
                    { product.statePublic==2 && buyerIs=="Yes" && <> <button onClick={buyProduct} className="btn btn-primary btn-long buy">Confirmar Entrega</button> <button className="btn btn-light wishlist"> <i className="fa fa-heart"></i> </button> </>}
                    
                    {cookies.get("idUser")== product.idUser && <> <button onClick={()=>navigate("/updatePost/"+idProduct)} className="btn btn-light wishlist"> <i className="fa fa-edit"></i> </button></>}
                    </div>
                    <hr/>
                    <div className="product-description">
                        { product.direction!=null &&
                         <div className="mt-2"> <span className="font-weight-bold">Direccion de envio cliente</span>
                            <p>{product.direction}</p>
                            
                        </div>}
                        { product.phone!=null &&
                         <div className="mt-2"> <span className="font-weight-bold">Telefono</span>
                            <p>{product.phone}</p>
                            
                        </div>}
                    <div className="mt-2"> <span className="font-weight-bold">Estado</span>
                            <p>{stateProduct[product.state]}</p>
                            
                        </div>
                        <div className="mt-2"> <span className="font-weight-bold">Descripcion</span>
                            <p>{product.description}</p>
                            
                        </div>
                        <div className="mt-2"> <span className="font-weight-bold">vendedor</span> </div>
                        <div className="d-flex flex-row align-items-center"> 
                            <a href={"/"+product.idUser} className='linkPost' style={{textDecoration: "none"}}>
                                <img src={(product.pathPhoto==null || product.pathPhoto=="")?"/account.jpg":hostapi+"/image/profile/"+product.pathPhoto} className="rounded-circle store-image"/>
                            </a>
                            <div className="d-flex flex-column ml-1 comment-profile">
                                    
                                        <div className="comment-ratings"> <i className="fa fa-star"></i> <i className="fa fa-star"></i> <i className="fa fa-star"></i> <i className="fa fa-star"></i> </div> <a href={"/"+product.idUser} className='linkPost' style={{textDecoration: "none",color:'black'}}><span className="username">{product.fullname}</span> </a> <small className="followers">0K seguidores</small>
                                    
                            </div>
                        </div>
                    </div>
                </div>
                <div className="card mt-2"> <span>Productos Similares:</span>
                    { "todavia"=="no publicar" && <div className="similar-products mt-2 d-flex flex-row">
                        <div className="card border p-1" style={ {width: "9rem",marginRight: "3px"} } > <img src="https://i.imgur.com/KZpuufK.jpg" className="card-img-top" alt="..."/>
                            <div className="card-body">
                                <h6 className="card-title">Q1,999</h6>
                            </div>
                        </div>
                        <div className="card border p-1" style={{width: "9rem",marginrRight: "3px"}}> <img src="https://i.imgur.com/GwiUmQA.jpg" className="card-img-top" alt="..."/>
                            <div className="card-body">
                                <h6 className="card-title">Q1,699</h6>
                            </div>
                        </div>
                        <div className="card border p-1" style={{width: "9rem",marginrRight: "3px"}}> <img src="https://i.imgur.com/c9uUysL.jpg" className="card-img-top" alt="..."/>
                            <div className="card-body">
                                <h6 className="card-title">Q2,999</h6>
                            </div>
                        </div>
                        <div className="card border p-1" style={{width: "9rem",marginrRight: "3px"}}> <img src="https://i.imgur.com/kYWqL7k.jpg" className="card-img-top" alt="..."/>
                            <div className="card-body">
                                <h6 className="card-title">Q3,999</h6>
                            </div>
                        </div>
                        <div className="card border p-1" style={{width: "9rem"}}> <img src="https://i.imgur.com/DhKkTrG.jpg" className="card-img-top" alt="..."/>
                            <div className="card-body">
                                <h6 className="card-title">Q999</h6>
                            </div>
                        </div>
                    </div>}
                </div>
            </div>
        </div>
    </div>
    </div>
</>         );
      } 
  