import React, { useEffect,useState } from 'react';

import { Link } from 'react-router-dom';
import 'react-slideshow-image/dist/styles.css'
import ProductPage from './productPage';
import PostProduct from './postProduct';
import AddMoney from './addMoney';
import Wizard from './wizard';
import Initial from './initial';
import Cookies from 'universal-cookie';
import {useNavigate} from 'react-router-dom';
import {Navbar, Nav, NavItem,NavDropdown,Container,Form,FormControl,Button} from 'react-bootstrap';
import axios from 'axios';
import "./../css/aspects.css";
import Landing from './landing';
var hostapi=process.env.REACT_APP_API_URL;

const slideImages = [
    'https://i.imgur.com/KZpuufK.jpg',
    'https://i.imgur.com/GwiUmQA.jpg',
    'https://i.imgur.com/DhKkTrG.jpg'
  ];
  

export default function BackgroundPage(props){
  const [page,setPage]=useState([]);
  const [isLanding,setIsLanding]=useState(false);
  const navigate=useNavigate();
  const cookies = new Cookies();
  useEffect(()=>{
    setPage(props.page)
    if(props.action!="productDetail" && (cookies.get("idUser")==null || cookies.get("idsession")==null || cookies.get("idUser")=="-1" || cookies.get("idsession")=="-1") )
      setPage(<Landing/>)  
    //setIsLanding(true) 
    //navigate("/login");   
  })
  const signup=async(evt)=>{
   // evt.preventDefault();
   // axios.get(hostapi+'/signup/'+cookies.get("idUser")+"/"+cookies.get("idsession"))
    
    await axios({  
      method: 'GET',
      url: hostapi+'/signup/'+cookies.get("idUser")+"/"+cookies.get("idsession"),
      headers: {
        'Content-Type': 'application/json'
      },

    }).then(res=>{
      //setSessionCookie(res.data,res);
      cookies.remove("idUser")
      cookies.remove("idsession")
      cookies.remove("pathPhoto")
      //navigate("/");
  
     // navigate("/");
    }).then(res=>{
      navigate("/");
    })
  }
  
  
    return (
      <>
      
          <link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.8.1/css/all.css" integrity="sha384-50oBUHEmvpQ+1lW4y57PTFmhCaXp0ML5d60M1M7uH2+nqUivzIebhndOJK28anvf" crossorigin="anonymous"/>
      
          <link rel="stylesheet" href="/newCarousel.css"/>
      
        
      <link href="/assets/dist/css/bootstrap.min.css" rel="stylesheet"/>
      <script src="https://cdnjs.cloudflare.com/ajax/libs/jquery/3.2.1/jquery.min.js"></script>
     
      
      <link href="/carousel.css" rel="stylesheet"/>
                  <header>
        <Navbar className="fixed-top" bg="light" expand="lg">
        <Container >
          <Navbar.Brand className="pageName" style={{fontSize: "25px"}} href="/">Efectup</Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" />
          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="me-auto my-2 my-lg-0"
              style={{ maxHeight: '100px' }}
              navbarScroll
            >
             
            </Nav>
            {cookies.get("idUser")!=null? <><button onClick={()=>navigate("/PostProduct")} style={{background:"#56baed"}} className="btn btn-primary btn-long buy">Subir Publicacion</button> <button className="btn btn-light wishlist"></button> </>:<></>}
            <form className="d-flex">
            {cookies.get("idUser")!=null?<><input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
                <span className="input-group-text border-0" style={{cursor: "pointer"}} id="search-addon">
                  <i className="fas fa-search"></i>
                </span></>:<> 
                <input type="submit" onClick={ ()=>navigate("/login")} className="buttonaspect" value="Ingresar"/>
                <input type="submit" onClick={ ()=>navigate("/register")} className="buttonaspect2" value="Registrarse GRATIS!"/>
                </>}
                
                
              </form>
              
            {cookies.get("idUser")!=null?

              
               <NavDropdown className="toggle" title={
                <img src={(cookies.get("pathPhoto")==null||cookies.get("pathPhoto")=="")?"account.png": hostapi+"/image/profile/"+ cookies.get("pathPhoto") } alt="" className="user-avatar-md rounded-circle"/>
             } id="navbarScrollingDropdown">
                 <NavDropdown.Item href="/wizard">Cuenta</NavDropdown.Item>
                 <NavDropdown.Item href="/monetization">Depositos/Retiros</NavDropdown.Item>
                 <NavDropdown.Item href="/buy">Compras</NavDropdown.Item>
                 <NavDropdown.Item href="/sell">Ventas</NavDropdown.Item>
                 <NavDropdown.Item href="/posted">Publicados</NavDropdown.Item>
                 <NavDropdown.Divider />
                 <NavDropdown.Item onClick={signup} href="#">
                 Salir
                 </NavDropdown.Item>
               </NavDropdown>
               :
                <></>
            }
           
          </Navbar.Collapse>
        </Container>
      </Navbar>
        
      </header>
      
      <main>
      
       
        <div className="container marketing">
      
          <hr className="featurette-divider"/>
      
          <div className="row featurette">
              
                
                  {page}
          
         
          </div>
      
          <hr className="featurette-divider"/>
      
      
        </div>
      
      
        <footer className="container">
          <p>&copy; 2022 desarrollado por Software Complex Solution &middot; <a href="/terms" style={{color:"black"}}>Terminos y Condiciones</a> &middot; <a href="#" >Privacidad</a></p>
        </footer> 
      </main>
      
      
          <script src="./assets/dist/js/bootstrap.bundle.min.js"></script>
          
      
      
      
         </>         );
  
         
      } 
  