import React, {useState ,useEffect} from 'react';

import { Link,useNavigate } from 'react-router-dom';

import axios from 'axios'
import Cookies from 'universal-cookie';
var hostapi=process.env.REACT_APP_API_URL;

export default function Wizard(props){

const [selectedFile, setSelectedFile] = useState(null);
const [selectedFileDocument, setSelectedFileDocument] = useState(null);
const [previewImage,setPreviewImage]=useState();

const [name, setName] = useState("");
const [surname, setSurname] = useState("");
const [direction, setDirection] = useState("");
const [phone, setPhone] = useState("");
const [gender, setGender] = useState("Masculino");
const [university, setUniversity] = useState("0");
const cookies = new Cookies();
const navigate=useNavigate();

const [step,setStep]=useState(["","visually-hidden-focusable"]);
const [stateStep,setStateStep]=useState(["active",""]);

useEffect(async()=>{
    if(cookies.get("pathPhoto")!=null)
        setPreviewImage((cookies.get("pathPhoto")==null || cookies.get("pathPhoto")=="")? "/account.png": hostapi+"/image/profile/"+ cookies.get("pathPhoto")); 

        //axios.get(hostapi+'/detailProfile/'+cookies.get("idUser")+"/"+cookies.get("idsession"))
        
        await axios({  
            method: 'GET',
            url:hostapi+'/detailProfile/'+cookies.get("idUser")+"/"+cookies.get("idsession"),
            headers: {
              'Content-Type': 'application/json'
            },
          }).then(res=>{
            console.log(res.data[0]);
            var userInformation=res.data[0];
            setName(userInformation.name)
            setSurname(userInformation.surname);
            setDirection(userInformation.direction)
            setPhone(userInformation.phone);
            setGender(userInformation.gender)
            setUniversity(userInformation.idUniversity)
          })
          
},[]);
const setImageFile=(e) =>{
    setSelectedFile(e.target.files[0])
    setPreviewImage(URL.createObjectURL(e.target.files[0])); 
}
const setDocumentFile=(e) =>{
    setSelectedFileDocument(e.target.files[0])
    //setPreviewImage(URL.createObjectURL(e.target.files[0])); 
}
const sendForm=async(evt)=>{
    evt.preventDefault();
    const formData=new FormData();
    formData.append("idUser",cookies.get("idUser"));
    formData.append("idSession",cookies.get("idsession"));
    formData.append("name",name);
    formData.append("surname",surname);
    formData.append("direction",direction);
    formData.append("phone",phone);
    formData.append("gender",gender);
    formData.append("photoFile",selectedFile);
    formData.append("university",university);
    console.log("name: "+name)
    //axios.post(hostapi+'/formProfile',formData)
    await axios({  
        method: 'POST',
        url:hostapi+'/formProfile',
        data:formData,
        headers: {
          'Content-Type': 'application/json'
        },
      }).then(res=>{
        if(res.data.pathPhoto!="")
            cookies.set("pathPhoto", res.data.pathPhoto, { path: '/' ,secure:true});
       // navigate("/");
       setStateStep(["","active"]);
       setStep(["visually-hidden",""]);
    })
}

const sendFormDocument=async(evt)=>{
    evt.preventDefault();
    const formData=new FormData();
    formData.append("idUser",cookies.get("idUser"));
    formData.append("idSession",cookies.get("idsession"));
    formData.append("pathDPI",selectedFileDocument);
    
   // axios.post(hostapi+'/formDocument',formData)
    
    await axios({  
        method: 'POST',
        url:hostapi+'/formDocument',
        data:formData,
        headers: {
          'Content-Type': 'application/json'
        },
      }).then(res=>{
        navigate("/");
    }).catch(
      res=>{
        navigate("/");
      }
    )
}
 
          return (
<>
<link href="wizard.css" rel="stylesheet"/>
<div class="row featurette">
      <div class="container-fluid">
      <div class="container">
        <div class="mt-2 mb-2 text-center">
            <h2>Pasos para completar el registro y ingresar en efectup</h2>
        </div>
        <ul class="step d-flex flex-nowrap">
      <li class={"step-item "+stateStep[0]}>
        <a href="#!" class="">Informacion Basica</a>
      </li>
      <li class={"step-item "+stateStep[1]}>
        <a href="#!" class="">Documento de Identificacion</a>
      </li>
      <li class="step-item ">
        <a href="#!" class="">Iniciar</a>
      </li>
     
    </ul> 
        
</div>
      </div>
      <div class={"profilecard  justify-content-around align-items-center "+step[0]}>
       
        <form action="" method="post">
            <div class="preview text-center d-flex justify-content-center">
                <img class="preview-img" src={previewImage} alt="Preview Image" width="200" height="200"/>
                <div class="browse-button" >
                    <input class="browse-input" onChange={setImageFile} type="file" required name="UploadedFile" id="UploadedFile" accept=".jpg,.png"/>
                </div>
                <span class="Error"></span>
            </div>
            <div class="form-group">
                <label>Nombre:</label>
                <input class="form-control" type="text" onChange={e=>setName(e.target.value)} name="name" value={name} required placeholder="Nombre"/>
                <span class="Error"></span>
            </div>
            <div class="form-group">
                <label>Apellido:</label>
                <input class="form-control" type="texto" onChange={e=>setSurname(e.target.value)} value={surname} name="surname" required placeholder="Apellido"/>
                <span class="Error"></span>
            </div>
            <div class="form-group">
                <label>Direccion(importante):</label>
                <input class="form-control" type="texto" onChange={e=>setDirection(e.target.value)} value={direction} name="direction" required placeholder="Tu direccion"/>
                <span class="Error"></span>
            </div>

            <div class="form-group">
                <label>Telefono(importante):</label>
                <input class="form-control" type="texto" onChange={e=>setPhone(e.target.value)} value={phone} name="phone" required placeholder="Tu telefono"/>
                <span class="Error"></span>
            </div>           
            <div className="form-group">
                <label for="titleUniversity">Universidad donde estudias o egresaste:</label>
                <select  onChange={e=>setUniversity(e.target.value)}  value={university} className="form-control" sytle={{size:10}} id="nameUniverity">
                  <option value='0'>No Aplica</option>
                  <option value='1'>Universidad Mariano Galvez de Guatemala</option>
                  <option value='2'>Universidad de San Carlos de Guatemala</option>
                  <option value='3'>Universidad Mesoamericana</option>
                  <option value='4'>Universidad Francisco Marroquín (UFM)</option>
                  <option value='5'>Universidad del Valle de Guatemala(UVG)</option>
                  <option value='6'>Universidad Rafael Landívar(URL)</option>
                  <option value='7'>Universidad San Pablo de Guatemala</option>
                  <option value='8'>Universidad Internaciones</option>
                  <option value='9'>Universidad Galileo</option>
                  <option value='10'>Universidad del Istmo</option>
                  <option value='11'>Universidad Da Vinci</option>
                  <option value='12'>Universidad Rural</option>
                  <option value='12'>Universidad Regional</option>

                </select>
       </div>
             <div class="form-group">
                <label>Genero:</label><br/>
                <label><input type="radio" onClick={e=>setGender("Masculino")} name="gender" required value="Masculino" checked={gender=="Masculino"} /> Masculino</label>
                <label><input type="radio" onClick={e=>setGender("Femenino")} name="gender" required value="Femenino" checked={gender=="Femenino"} /> Femenino</label>
               <span class="Error"></span>
            </div>
            <div class="form-group">
                <input class="btn btn-primary btn-block" onClick={sendForm} type="submit" value="Siguiente"/>
            </div>
        </form>
      </div>  
      <div class={"profilecard  justify-content-around align-items-center "+step[1]}>
       
        <form action="" method="post">
            <div class="preview text-center d-flex justify-content-center">
                <div class="mb-3">
                    <label for="formFile" class="form-label">Ingresa tu Documento de identificacion( DPI, Licencia de conducir o Passaporte)</label>
                    <input class="form-control" id="formFile" onChange={setDocumentFile} type="file" required name="UploadedFile" accept=".jpg,.png,.pdf" />
                </div>
                
               
                <span class="Error"></span>
            </div>
            
            
           
    
            <div class="form-group text-center d-flex justify-content-center">
                <div class="m-3">
                  <input class="btn btn-outline-primary btn-block" onClick={()=>{navigate("/")}} type="submit" value="Omitir"/>
                </div>
                <div class="m-3">
                  <input class="btn btn-primary btn-block" onClick={sendFormDocument} type="submit" value="Enviar"/>
                </div>
            </div>
        </form>
    </div>  
      
    </div>
</>
            );
      } 
  