import logo from '../logo.svg';
import '../App.css';
import {useEffect } from 'react';
import {Routes,Route,Navigate} from "react-router-dom";
import Principal from '../components/principal';
import BackgroundPage from '../components/backgroundPage';
import ProductPage from '../components/productPage';
import PostProduct from '../components/postProduct';
import Wizard from '../components/wizard';
import AddMoney from '../components/addMoney';
import Register from '../components/register';
import Cookies from 'universal-cookie';
import Login from '../components/login';
import Initial from '../components/initial';
import Recovery from '../components/recovery';
import Terms from '../components/terms';

import ReactGA from 'react-ga';
import Profile from '../components/profile';
  const TRACKING_ID = "G-WWNR2R31EP"; // OUR_TRACKING_ID
  ReactGA.initialize(TRACKING_ID);

//import MainProfile from '../components/profiles/sunmoon/MainProfile';

const App=()=> {  
  const cookies = new Cookies();
  useEffect(() => {
    ReactGA.pageview(window.location.pathname + window.location.search);
  }, []);  
  return (
      <Routes>
        <Route exact path="/login" element={<Login/>} />
        <Route exact path="/buy" element={<BackgroundPage page={<Initial action="buy" />} />} />
        <Route exact path="/sell" element={<BackgroundPage page={<Initial action="sell" />} />} />
        <Route exact path="/posted" element={<BackgroundPage page={<Initial action="posted" />} />} />
        <Route exact path="/detailProduct/:idProduct" element={<BackgroundPage page={<ProductPage  />} action="productDetail" />} />
        <Route exact path="/postProduct" element={<BackgroundPage page={<PostProduct/>} />} />
        <Route  path="/updatePost">
          <Route  path=":idProduct" element={<BackgroundPage page={<PostProduct/>} />} />
        </Route>
        <Route exact path="/wizard" element={<BackgroundPage page={<Wizard/>} />} />
        <Route exact path="/monetization" element={<BackgroundPage page={<AddMoney/>} />} />
        <Route exact path="/register" element={<Register/>} />
        <Route exact path="/terms" element={<Terms/>} />
        <Route  path="/recovery">
          <Route index  element={<Recovery/>} />
          <Route  path=":idUser/:tokenRecovery" element={<Recovery/>} />
        </Route>
        <Route  path="/">
           <Route index  element={<BackgroundPage page={<Initial action="initial"/>} />} />
           <Route  path=":idUser" element={<BackgroundPage page={<Profile action="" />} />} />
        </Route>
        
        </Routes>
     );
    
}


export default App;
